//#region Global Imports
import * as React from 'react';
import { bindActionCreators, Dispatch } from 'redux';
// import NEOLink from 'next/link';
//#endregion Global Imports

//#region Interface Imports
import { Heading } from 'Components/UI/Heading';
import { Store } from 'Components/Stores/IStore';
import { IHomePage } from './IHomePage';
import { HomeActions } from './HomeActions';

//#endregion Interface Imports

export class HomePage extends React.Component<IHomePage.IProps, IHomePage.IState> {
	constructor(props: IHomePage.IProps) {
		super(props);
	}

	public render(): JSX.Element {
		return (
			<div className="title">
				Hello!
				<Heading text="World!" />
			</div>
		);
	}
}

const mapStateToProps = (state: Store) => state.home;

const mapDispatchToProps = (dispatch: Dispatch) => ({
	Map: bindActionCreators(HomeActions.Map, dispatch),
});

export { mapStateToProps, mapDispatchToProps };
